@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
body {
  background: #323232
}
.bg {
  position: relative;
  width: 100%;
  height: 100vh;
  background: #2bbaff;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  overflow: hidden;
}
.bg h2 {
  font-size: 5em;
  color: #fff;
}
.SkewOne {
  position: absolute;
  bottom: -100%;
  left: 50%;
  width: 100%;
  height: 100%;
  background: #323232;
  -webkit-transform: skewY(-15deg);
          transform: skewY(-15deg);
  -webkit-transform-origin: top left;
          transform-origin: top left;
}
.SkewTwo {
  position: absolute;
  bottom: -100%;
  right: 50%;
  width: 100%;
  height: 100%;
  background: #323232;
  -webkit-transform: skewY(15deg);
          transform: skewY(15deg);
  -webkit-transform-origin: top right;
          transform-origin: top right;
}
section {
  padding: 100px;
}
section h2 {
  color: #fff;
  font-size: 2em;
  margin-bottom: 10px;
}
section p {
  color: #fff;
  font-size: 1.2em;
}
